@import "../variables";

.home{
    margin-top: 50px;

    @include breakpoint(mediumWidth){
      display: flex;
      margin-top: 75px;
    }
}

@include breakpoint(mediumWidth){
  .left, .right{
      width: calc(50% - 100px);
  }
}

.left{
    margin-left: 25px;
    margin-right: 25px;
    margin-bottom: 50px;
    opacity: 0;
    
    @include breakpoint(mediumWidth){
      position: fixed;
      margin-left: 50px;
      margin-right: 0;
    }
}

.left h1{
    margin-bottom: 5px;
    font-size: 2.5em;

    @include breakpoint(mediumWidth){
      font-size: 3em;
    }
}

.left h2{
    font-size: 1.25em;

    @include breakpoint(mediumWidth){
      font-size: 1.5em;
    }
}

.left p{
    font-size: 1.5em;
    font-style: italic;

    @include breakpoint(mediumWidth){
      font-size: 2em;
    }
}

.right{
    margin-left: 25px;
    margin-right: 25px;
    opacity: 0;

    @include breakpoint(mediumWidth){
      margin-left: 50%;
      margin-right: 50px;
    }
}

.projectGrid *{
    list-style: none;
}

.project{
    margin-bottom: 40px;
}

.project h2{
    font-size: 2em;
}

.project ul{
    display: flex;
    margin-top: 5px;
}

.project li{
    margin-right: 15px;
}

.back{
    margin-left: 25px;
    margin-bottom: 20px;
    font-size: 1.25em;

    @include breakpoint(mediumWidth){
      position: fixed;
      margin-left: 50px;
      margin-top: -50px;
    }
}



.introAnimation {
    animation: introAnimation ease-in-out .25s forwards;
}

.slideOut {
    animation: slideOut ease-in-out .25s forwards;
}

.slideOutDelay {
    opacity: 1;
    animation: slideOutDelay ease-in-out .25s forwards .1s;
}

.slideIn {
    opacity: 0;
    animation: slideIn ease-in-out .25s forwards;
}

.slideInDelay {
    opacity: 0;
    animation: slideInDelay ease-in-out .25s forwards .1s;
}

.slideOutReverse {
    opacity: 1;
    animation: slideOutReverse ease-in-out .25s forwards;
}

.slideOutDelayReverse {
    opacity: 1;
    animation: slideOutDelayReverse ease-in-out .25s forwards .1s;
}

.slideInReverse {
    opacity: 0;
    animation: slideInReverse ease-in-out .25s forwards;
}

.slideInDelayReverse {
    opacity: 0;
    animation: slideInDelayReverse ease-in-out .25s forwards .1s;
}

@include breakpoint(mediumWidth){
  .left #blur{
    filter: blur(2px);
  }

  .blur{
      animation: blur ease-in-out .25s forwards;
  }
}

@keyframes introAnimation {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
}

@keyframes slideOut {
    0% {
      opacity: 1;
      left: 0;
    }
    100% {
      opacity: 0;
      left: -5px;
    }
}

@keyframes slideOutDelay {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
      margin-left: calc(50% - 10px);
    }
}

@keyframes slideIn{
    0% {
      opacity: 0;
      left: 5px;
    }
    100% {
      opacity: 1;
      left: 0;
    }
}

@keyframes slideInDelay {
    0% {
      opacity: 0;
      margin-left: calc(50% + 10px);
    }
    100% {
      opacity: 1;
      margin-left: 50%;
    }
}

@keyframes slideInReverse {
    0% {
      opacity: 0;
      margin-left: calc(50% - 10px);
    }
    100% {
      opacity: 1;
      
    }
}

@keyframes slideInDelayReverse {
    0% {
      opacity: 0;
      left: -5px;
    }
    100% {
      opacity: 1;
      left: 0;
    }
}

@keyframes slideOutReverse{
    0% {
      opacity: 1;
      margin-left: 50%;
    }
    100% {
      opacity: 0;
      margin-left: calc(50% + 10px);
    }
}

@keyframes slideOutDelayReverse {
    0% {
      opacity: 1;
      left: 0;
    }
    100% {
      opacity: 0;
      left: 5px;
    }
}

@keyframes blur {
    0% {
        filter: blur(2px);
    }
    100% {
        filter: blur(0px);
    }
}